.fouroh-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: linear-gradient(rgba(40, 55, 77, 0.7),rgba(40, 55, 77, 0.7)), url("http://i.giphy.com/4Ra6jnDDOVdNS.gif");
  background-size: cover;

  a {
    color: #5CE4E6;
    text-decoration: none;
    border-bottom: 2px solid rgba(92, 228, 230, 0.5);
    transition: all 0.2s ease-in-out;
    cursor: pointer;

    &:hover {
      border-bottom: 2px solid #5CE4E6;
    }
  }

  .fouroh-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    height: 100vh;

    h1 {
      font-size: responsive [2rem] [4rem];
      font-range: 320px 1280px;
      color: $bone-white;
      margin: 0 auto;
      padding: 0;
      text-align: center;
      flex:none;
      letter-spacing: -0.05rem;
      width: 80%;
      line-height: 1.2;
    }

    h2 {
      width: 100%;
      text-align: center;
      flex:none;
    }
  }


}
