/*@lost flexbox flex;*/
@lost gutter 16px;

body {
  background-color: #7133bf;
}

::selection {
 color: #fff;
 background: #00d699;
}

.js-mark {
  position: absolute;
  left: 3em;
  top: 3em;

  @media (--tablet-viewport) {
    left: 1.5em;
    top: 1.5em;
  }

  @media (--phone-viewport) {
    left: 1.5em;
    top: 1.5em;
  }
}

.svg-wrap {
  position: absolute;
  width: 0px;
  height: 0px;
  overflow: hidden;
}

.container {
  background: $bone-white;
  display: block;
  height: 95vh;
  margin: 1.25em;
  position: relative;

  @media (--phone-viewport) {
    margin: 0.5em;
    height:100%;
  }
}

.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 95vh;
  color: #7133bf;

  .splash-panel {

    h1 {
      @media (--tablet-viewport) {
        font-size: 1.5rem;
      }

      @media (--phone-viewport) {
        font-size: 1.5rem;
        margin-bottom: 0;
      }
    }

    h2 {
      @media (--tablet-viewport) {
        font-size: 1.25rem;
      }

      @media (--phone-viewport) {
        font-size: 1.25rem;
        margin-bottom: 0;
      }
    }
  }

  @media (--phone-viewport) {
    display: block;
    align-items: none;
    justify-content: none;
    height: 100%;
    padding-top: 5em;
  }

  .svg-triangle {
    fill: #7133bf;
    width: 150px;
    height: 150px;
    position: absolute;
    top: 5%;
    right: 10%;
    opacity: 0.1;
    animation: triangle-float-splash 20s infinite ease-in-out;
    /*mix-blend-mode: screen;*/
  }

  .svg-cross {
    fill: #7133bf;
    width: 150px;
    height: 150px;
    position: absolute;
    opacity: 0.1;
    animation: cross-float-splash 25s infinite ease-in-out;
    /*mix-blend-mode: screen;*/
  }



  section {
    lost-center: 775px;

    @media (--tablet-viewport) {
      padding: 0 1.5em;
    }

    @media (--phone-viewport) {
      padding: 1em 1.5em 0 1.5em;
    }

    .link-svgline {
      lost-column: 8/8;

    }
    .expressions-list {
      lost-column: 2/8;

      li {
        font-size: 1.0625rem;
      }

      &:nth-child(2) {
        padding-top: 4.3em;
        lost-offset: 1/8;

        @media (--phone-viewport) {
          float: left !important;
          padding-top:1em;
          lost-offset: 0;
          margin-right: 0;

        }

        @media (--tablet-viewport) {
        padding-top:1em;
        }
      }

      @media (--phone-viewport) {
        lost-column: 1/1;
      }

      @media (--tablet-viewport) {
        lost-column: 8/8;
      }
    }

    .about-site {
      lost-column: 3/8;

      p {
        font-size: 1rem;

        &:last-child {
          margin-bottom: 1.5em;
        }
      }

      @media (--phone-viewport) {
        lost-column: 1/1;
      }

      @media (--tablet-viewport) {
        lost-column: 8/8;
      }
    }
  }
}


/* SVG line */
.link-svgline {
  a {
    position: relative;
    display: inline-block;
    outline: none;
    color: $funk-green;
    vertical-align: bottom;
    text-decoration: none;
    white-space: nowrap;
    border: 0;
    cursor: pointer;
  }
}

.link-svgline a svg.link-svgline {
	position: absolute;
	top: 100%;
	left: 0;
	overflow: hidden;
	margin: 0;
	width: 100%;
	height: 20px;
	transition: stroke-dashoffset 0.35s ease-in-out;
	transform: translateY(-90%);
	fill: none;
	stroke: rgba(0, 214, 153, 0.4);
	stroke-width: 3;
	stroke-dasharray: 400px;
	stroke-dashoffset: 400px;
}

.link-svgline a:hover svg.link-svgline {
	stroke-dashoffset: 0px;
}



/* About Me Panel */
.about-panel {
  background: $punk-blue;
  display: block;
  height: 95vh;
  margin: 1.25em;
  position: relative;

  @media (--phone-viewport) {
    display: block;
    align-items: none;
    justify-content: none;
    height: 100%;
    padding-top: 5em;
  }

  .svg-triangle {
    fill: $bone-white;
    width: 150px;
    height: 150px;
    position: absolute;
    top: 5%;
    right: 20%;
    opacity: 0.3;
    animation: triangle-float 15s infinite ease-in-out;
    /*mix-blend-mode: screen;*/
  }

  .svg-circle {
    fill: $bone-white;
    width: 400px;
    height: 400px;
    position: absolute;
    top: 5%;
    left: 20%;
    opacity: 0.3;
    animation: flotate 20s infinite ease-in-out;
    /*mix-blend-mode: screen;*/
  }

  .svg-square {
    fill: $bone-white;
    width: 220px;
    height: 220px;
    position: absolute;
    top: 35%;
    left: 13%;
    opacity: 0.1;
    animation: square-float 25s infinite ease-in-out;
    /*mix-blend-mode: screen;*/
  }


  section {
    lost-center: 775px;

    .profile-blurb {
      lost-column: 8/8;
      color: $bone-white;
    }

    .img-wrap {
      lost-column: 2/8;

      @media (--phone-viewport) {
        lost-column: 1/1;
        text-align: center;
        margin-bottom: 5em;
      }
    }

    .profile-wrap {
      lost-column: 6/8;

      p {
        @media (--phone-viewport) {
          padding-right: 5%;
        }
      }

      @media (--phone-viewport) {
        lost-column: 1/1;
        width: 95%;
        padding-right: 0;
      }
    }
  }

  .about-hero {
    font-family: $heading-font-family;
    font-size: 4.5rem;
    position: absolute;
    left: -283px;
    top: 39%;
    transform: rotate(-90deg);
    width: 700px;
    color: $bone-white;
    letter-spacing: 5px;

    @media (--phone-viewport) {
      display: none;
    }
  }

  .contact-links {
    font-family: $mono-font-family;
    font-size: 1rem;
    position: absolute;
    right: -210px;
    top: 39%;
    transform: rotate(90deg);
    width: 480px;
    color: $bone-white;
    letter-spacing: 2px;

    @media (--phone-viewport) {
      transform: rotate(0deg);
      position: absolute;
      left: 0;
      top: 40%;
      width: 100%;
      text-align: center;
    }

    @media (--phone-large-viewport) {
      top: 42%;
    }

    @media (--tablet-viewport) {
      transform: rotate(0deg);
      position: absolute;
      left: 25.5%;
      top: 75%;
      width: auto;
      text-align: left;
      right: 0;
    }

  }

  .older-portfolio {
    position: absolute;
    bottom: 0;
    right: 10px;
    max-width: 100%;
    color: $bone-white;

    @media (--phone-viewport) {
      position: relative;
      left:0;
      top: 5em;
      max-width: 100%;
      padding-bottom: 1em;
    }
  }

  .heading-wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 95vh;
      position: relative;

      .img-circle {
        border-radius: 50%;
        height: 150px;
        width: 150px;

        @media (--phone-viewport) {
          height: 250px;
          width: 250px;
        }
      }

      .profile-wrap {

        .profile-intro {
          font-size: 1.5rem;
          line-height: 1.6;
          margin: 0;
          padding: 0 0 1em 0;

          @media (--phone-viewport) {
            padding: 10% 5% 7% 0;
          }
        }

        p {
          font-size: 1.0625rem;
          line-height: 1.6;
        }
      }

  }
} /* Close .about-panel */

/* Keyframe animations for SVGs */

@keyframes triangle-float-splash {
  0% {
    transform: translateY(0%) rotate(90deg);
    top: 5%;
    right: 30%;
  }
  50% {
    transform: translateY(200px) rotate(360deg);
    top: 5%;
    right: 10%;
  }
  100% {
    transform: translateY(0%) rotate(90deg);
    top: 5%;
    right: 30%;
  }
}

@keyframes cross-float-splash {
  0% {
    transform: translateY(0%) rotate(45deg);
    bottom: 10%;
    left: 15%;
  }
  50% {
    transform: translateY(-450px) rotate(720deg);
    bottom: 15%;
    left: 5%;
  }
  100% {
    transform: translateY(0%) rotate(45deg);
    bottom: 10%;
    left: 15%;
  }
}

@keyframes triangle-float {
  0% {
    transform: translateY(0%) rotate(45deg);
    top: 5%;
    right: 20%;
  }
  50% {
    transform: translateY(200px) rotate(360deg);
    top: 5%;
    right: 10%;
  }
  100% {
    transform: translateY(0%) rotate(45deg);
    top: 5%;
    right: 20%;
  }
}

@keyframes square-float {
  0% {
    transform: translateY(0%) rotate(-45deg);
    top: 35%;
    left: 13%;
  }
  50% {
    transform: translateY(50px) rotate(-360deg);
    top: 65%;
    left: 3%;
  }
  100% {
    transform: translateY(0%) rotate(-45deg);
    top: 35%;
    left: 13%;
  }
}
