@font-face {
    font-family: 'paul_grotesk_regular';
    src: url('../fonts/paulgrotesk-regular-webfont.woff2') format('woff2'),
         url('../fonts/paulgrotesk-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'paul_grotesk_bold';
    src: url('../fonts/paulgrotesk-bold-webfont.woff2') format('woff2'),
         url('../fonts/paulgrotesk-bold-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

body {
  /* Declaring font size and line heights (unitless) */
  font: 1rem/1.5 $base-font-family; /* 16px = 1 rem */
  color: $punk-blue;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-weight: normal;
  font-size: 2.25rem;
  line-height: 1.5;
  margin-bottom: 1vr;
  padding-top: .5vr;
}

h2 {
  font-weight: normal;
  font-size: 1.75rem;
  line-height: 1.5;
  margin-bottom: .5vr;
  padding-top: .25vr;
}

h3 {
  font-family: $heading-font-family;
  font-weight: normal;
  font-size: 1.25rem;
  line-height: 1.5;
  margin-bottom: .5vr;
  padding-top: .25vr;
}

p {
  font-weight: normal;
  font-size: 1rem;
  line-height: 1.5;
  margin: 0;
  padding: 0 0 1em 0;
}


ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

li {
  padding: 0 0 0.5em 0;
}

p, li, span {
  a {
    color: $funk-green;
    text-decoration: none;
    /*border-bottom: 2px solid rgba(0, 214, 153, 0.4);*/
    /*transition: all 0.2s ease-in-out;*/
    cursor: pointer;
    position: relative;
    display: inline-block;
    z-index: 10;

    &:hover {
      color: $bone-white;
    }

    &:hover:before {
      height: 100%;
      width: auto;
      left: -3px;
      right: -3px;
    }

    &:before {
      content: " ";
      display: block;
      position: absolute;
      width: 100%;
      height: 2px;
      background: $funk-green;
      bottom: 1px;
      z-index: -1;
    }
  }
}
